@import "./products.scss";
@import "../../components/statistics.scss";
@import "./about-the-use-of-the-product.scss";
@import "./about-the-manufacturer.scss";
.main-page {
  .banner-1 {
    min-height: 740px;
    .banner-container__title {
      margin-top: 47px;
      margin-right: 10px;
      @include rale-800-45;
      max-width: 1100px;
      @media (max-width: $mobile) {
        line-height: 30px;
        margin-right: none;
        margin-top: 30px;
      }
      @media (max-width: 400px) {
        font-size: 20px;
      }
    }

    .banner-container {
      gap: 0;
    }
    .banner-container__description {
      margin-top: 28px;
      margin-right: 20px;
      @include rale-500-24-i;
      @media (max-width: $mobile) {
        margin-top: 5px;
      }
    }
    .banner-container__icon-play.icon-play {
      margin-top: 70px;
      margin-right: 15px;
      @media (max-width: $mobile) {
        margin-top: 20px;
      }
    }
  }

  .body-banner.banner-2 {
    height: 740px;
    &:hover {
      cursor: auto;
      background: rgba(13, 12, 12, 0.55);
    }
    .banner-container {
      gap: 0;
      &__icon-eco {
        color: $white;
        margin-left: 1px;
        margin-top: 2.2%;
        font-size: 74px;
      }
      &__title {
        margin-top: 45px;
        margin-right: 17px;
        @include rale-800-45;
      }
      &__description {
        margin-top: 20px;
        margin-left: 6px;
        @include rale-500-22;
        max-width: 700px;
        line-height: 30px;
      }
    }
  }
}
