.news-and-articles {
  .page-banner_litle-unbackground {
    height: 224px;
    display: flex;
    justify-content: center;
    padding-top: 67px;
    .banner-container {
      &__title {
        @include rale-800-45;
      }
      &__description {
        @include rale-500-18;
        text-align: center;
        margin-top: 24px;
      }
    }
  }

  .current-page-info {
    &__container {
      .current-page-info__page-name {
        color: $gray;
        &:hover {
        }
        &:active {
        }
        &:focus {
        }
      }
    }
  }

  .swipers-news-section {
    padding-bottom: 90px;
    &__container {
      .swiper-news-block-swiper-big {
        overflow: hidden;
        .swiper-wrapper.news-block-swiper__wrapper {
        }
        .news-block-swiper__slide {
          display: flex;
          gap: 30px;
          img {
            max-width: 849px;
            max-height: 526px;
            width: 100%;
            height: 100%;
            object-fit: cover;
          }
          .slide-text-block {
            padding: 7px 0px 27px 20px;
            height: 100%;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            &__date {
              margin-top: 64px;
              @include mont-500-13;
              color: $gray;
            }
            &__title {
              @include rale-700-22;
              margin-top: 20px;
              max-width: 320px;
              line-height: 27px;
              letter-spacing: 0;
              margin-bottom: 12px;
            }
            &__description {
              @include rale-500-16;
              max-width: 500px;
              margin-top: 10px;
              line-height: 24px;
            }
            &__text-button {
              display: block;
              margin-top: 20px;
              &::after {
                content: "→";

                margin-left: 8px;
                font-size: 21px;
              }
              &:hover {
              }
              &:active {
              }
              &:focus {
              }
            }
          }
        }
      }
      .swiper-container.news-block-swiper__swiper-news-block-swiper.swiper-news-block-swiper {
        margin-top: 60px;
        overflow: hidden;
        .swiper-wrapper.news-block-swiper__wrapper {
        }
        .swiper-slide.news-block-swiper__slide {
          background-color: $white;
          display: flex;
          flex-direction: column;
          justify-content: space-between;
          height: initial;
          img {
            width: 100%;
            height: 270px;
            object-fit: cover;
          }
          .slide-text-block {
            padding: 23px 23px 27px 0px;
            height: 100%;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            &__date {
              margin-top: 1px;
              @include mont-500-13;
              color: $gray;
            }
            &__title {
              @include rale-700-16;
              margin-top: 10px;
              max-width: 370px;
              line-height: 24px;
            }
            &__description {
              @include rale-500-14;
              max-width: 350px;
              margin-top: 9px;
            }
            &__text-button {
              display: block;
              margin-top: 20px;
              &::after {
                content: "→";

                margin-left: 8px;
                font-size: 21px;
              }
              &:hover {
              }
              &:active {
              }
              &:focus {
              }
            }
          }
        }
      }
      .video-block {
        margin-top: 28px;
        .icon-play {
          border: 2px solid $white;
          border-radius: 50%;
          width: 61px;
          height: 61px;
          display: flex;
          justify-content: center;
          align-items: center;
          &::before {
            margin-left: 5px;
            font-size: 20px;
            color: $white;
          }
          &:hover {
            border-color: rgba($color: #eeeeee, $alpha: 1);
            &::before {
              color: rgba($color: #eeeeee, $alpha: 1);
            }
          }
        }
        position: relative;
        max-height: 554px;
        height: 100%;
        overflow: hidden;
        .text-block {
          text-align: center;
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
          background-color: rgba(0, 0, 0, 0.4);
          cursor: pointer;
          &:hover {
            background-color: rgba(0, 0, 0, 0.521);
          }
        }
        .text-block {
          display: flex;
          flex-direction: column;
          justify-content: center;
          align-items: center;
          .icon-play {
            margin-top: 108px;
          }
          &__title {
            max-width: 563px;
            margin-top: 28px;
            font-family: "Montserrat";
            font-style: normal;
            font-weight: 800;
            font-size: 22px;
            line-height: 27px;
            letter-spacing: 0.01em;
            color: #ffffff;
          }
          &__text {
            margin-top: 12px;
            max-width: 563px;
            font-family: "Montserrat";
            font-style: normal;
            font-weight: 500;
            font-size: 14px;
            line-height: 21px;
            text-align: center;
            color: #ffffff;
            margin-right: 5px;
          }
          strong {
            letter-spacing: -0.01em;
          }
        }
      }
      .rectangle.unbackground {
        margin-top: 70px;
        padding: 17px 37px 17px 39px;
        &:hover {
        }
        &:active {
        }
        &:focus {
        }
      }
    }
  }
  .body-contacts {
    background-color: #fbfbfb;
    padding-bottom: 90px;
  }
  .footer{
    padding-top: 45px;
  }
}
