button,
.button {
  font-family: "Raleway";
  font-size: 14px;
  line-height: 16px;
  cursor: pointer;
}
button.rectangle,
.button.rectangle {
  font-weight: 700;
  letter-spacing: 0.02em;
}
button.rectangle.background,
.button.rectangle.background {
  background: #337d5a;
  border: 1.5px solid #337d5a;
  padding: 17px 27px;
  color: #ffffff;
}
button.rectangle.background:hover,
.button.rectangle.background:hover {
  background: #00572e;
}
button.rectangle.background:active,
.button.rectangle.background:active {
  background: none;
  color: #337d5a;
}
button.rectangle.unbackground,
.button.rectangle.unbackground {
  background: none;
  border: 1.5px solid #337d5a;
  color: #337d5a;
  padding: 17px 27px;
}
button.rectangle.unbackground:hover,
.button.rectangle.unbackground:hover {
  color: #00572e;
  border-color: #00572e;
}
button.rectangle.unbackground:active,
.button.rectangle.unbackground:active {
  background: #337d5a;
  color: #ffffff;
}
button.circle,
.button.circle {
  border-radius: 50%;
}
button.circle.background,
.button.circle.background {
  background: #93b474;
  color: #ffffff;
}
button.circle.background:active,
.button.circle.background:active {
  background: #337d5a;
}
button.circle.unbackground,
.button.circle.unbackground {
  border: 2px solid #1a2f3f;
  color: #1a2f3f;
  background: none;
}
button.circle.unbackground:active,
.button.circle.unbackground:active {
  background: #337d5a;
  color: #ffffff;
  border: 2px solid #337d5a;
}
button.text,
.button.text {
  border: none;
  background: none;
  font-weight: 600;
  letter-spacing: 0.01em;
  color: #595959;
}
button.text:active,
.button.text:active {
  color: #337d5a;
}

