.wholesale-and-corporate-clients {
  .page-banner.page-banner_big {
    img {
    }
    .banner-container {
      justify-content: flex-start;
      margin-top: 220px;
      &__icon-eco {
      }
      &__title {
        display: block;
        margin-top: 26px;
      }
      &__description {
        margin-top: 30px;
      }
    }
  }

  .page-banner.page-banner_medium {
    img {
    }
    .banner-container.container {
      justify-content: flex-start;
      margin-top: 240px;
      &__icon-eco {
      }
      &__title {
        margin-top: 20px;
      }
      &__description {
      }
    }
  }

  .text-block-with-tabs-block {
    padding-top: 83px;
    .text-block-with-tabs-block__text-block {
        @media (max-width: 1150px) {
            flex-direction: column;
        }
      display: flex;
      justify-content: space-between;
      .text-block-with-tabs-block__title {
        @include rale-800-45;
        max-width: 410px;
        line-height: 52px;
        display: block;
        @media (max-width: 1150px) {
            max-width: none;
            text-align: center;
        }
      }
      .text-block-with-tabs-block__description-blocks {
        flex: 0 0 59.6%;
        display: flex;
        justify-content: space-between;
        gap: 20px;
        @media (max-width: 1150px) {
            flex: auto;
            justify-content: space-around;
            margin-top: 20px;
        }
        @media (max-width: 750px) {
            flex-direction: column;
            align-items: center;
        }
        .text-block-with-tabs-block__description-block {
          &:first-child {
            max-width: 355px;
            width: 100%;
          }
        }
      }
      .text-block-with-tabs-block__description-block {
        max-width: 330px;
        display: flex;
        flex-direction: column;
        padding-top: 10px;
        gap: 24px;
        .text-block-with-tabs-block__description {
        }
      }
    }
    .tabs-block {
      padding-top: 130px;
      padding-bottom: 90px;
      hr {
        border-color: rgba($color: #000000, $alpha: 0.08);
      }
      &__container {
        .tab-content-block {
            padding-top: 35px;
        }
        .tab-bar {
          max-width: none;
        }
        .tabs-block__text-block-with-img {
          gap: 20px;
          .tabs-block__text-block {
            padding-top: 30px;
            width: 100%;
            max-width: 450px;
            min-width: 330px;
            .rectangle.background{
                margin-top: 40px;
                padding: 17px 44px 17px 43px;
            }
          }
          .tabs-block__img-block {
            width: 100%;
          }
          .tabs-block__text-blocks {
            gap: 24px;
            .tabs-block-icon-text-block__text {
            }
            
          }
          img {
          }
        }
        .tabs-block__text-block-with-img {
          .tabs-block__text-block {
          }
          .tabs-block__img-block {
            max-width: 761px;
            width: 100%;
          }
          .tabs-block__text-blocks {
            .tabs-block-icon-text-block__text {
            }
          }
          img {
            width: 100%;
            height: 100%;
            object-fit: cover;
          }
        }
        .tab-bar-item.tab-button {
          padding-bottom: 15px;
          text-align: start;
          line-height: 24px;
          @media (max-width: 1100px) {
            flex-direction: column;
            align-items: center;
            justify-content: end;
            text-align: center;
            @media (max-width: 770px) {
              font-size: 15px;
              line-height: 20px;
            }
            @media (max-width: 600px) {
              font-size: 14px;
            }
          }
          .icon-sweets {
            font-size: 43px;
          }
          .icon-supermarket {
            font-size: 37px;
          }

          .icon-shop {
            font-size: 37px;
          }
          .icon-dinner-table {
            font-size: 49px;
          }
          .icon-dumbbell {
            font-size: 45px;
          }
          &:hover {
          }
          &:active {
          }
          &:focus {
          }
        }
      }
    }
  }
}
