.reset-password {
  &__container {
    display: flex;
    align-items: center;
    flex-direction: column;
    padding-top: 110px;
    padding-bottom: 95px;
    .reset-password__title {
      @include rale-800-32;
      margin-bottom: 27px;
      letter-spacing: 0.01em;
      word-spacing: 0.04em;
      text-align: center;
    }
    .blocks-reset-password {
      display: flex;
      flex-direction: column;
      .block-reset-password {
        display: flex;
        align-items: center;
        flex-direction: column;
        &__title {
            @include rale-600-18;
            
        }
        &__description {
            margin-top: 10px;
            letter-spacing: 0.02em;
            color: $gray;
            @include rale-500-15;
            max-width: 410px;
            display: block;
            text-align: center;
            line-height: 20px;
        }
        &__input {
          border: 0.5px solid;
          border-color: rgba($color: #000000, $alpha: 0.15);
          padding: 17px 20px 12px 10px;
          max-width: 420px;
          max-height: 47px;
          width: 100%;
          height: 100%;
          &.email{
            margin-top: 17px;
          }
        }
        &:nth-child(2){
            margin-top: 50px;
            .block-reset-password__description{
                text-align: start;
            }
        }
      }
      .button.rectangle.background {
        margin-top: 15px;
        padding: 14px 45px 12px 47px;
        @include rale-700-16;
        &:hover {
        }
        &:active {
        }
        &:focus {
        }
      }
    }
  }
}
